import { useState } from "react";
import { useZxing } from "react-zxing";
import "./Qr.css";
import bgdot from "../Assets/bgdot.png"; // Import the background image

export const BarcodeScanner = () => {
  const [result, setResult] = useState("");
  const { ref } = useZxing({
    onDecodeResult(result) {
      setResult(result.getText());
      // Redirect to the scanned URL (assuming it's a valid URL)
      if (isValidURL(result.getText())) {
        window.location.href = result.getText();
      }
    },
  });

  // Function to check if a given string is a valid URL
  const isValidURL = (url) => {
    try {
      new URL(url);
      return true;
    } catch (error) {
      return false;
    }
  };

  const backgroundStyle = {
    backgroundImage: `url(${bgdot})`, // Use the imported image as the background
    backgroundSize:"cover",
    
    height: "100vh",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  };

  return (
    <div className="background" style={backgroundStyle}>
      <div className="video-container">
        <video className="video" ref={ref} />
        <p>
          <span className="qr-text">Scan your QR!</span>
        </p>
      </div>
    </div>
  );
};

export default BarcodeScanner;
